<template>
  <div class="container">
    <div class="top">
      <div class="content">
        <el-button
          @click="startTiming(false, inputMinites, inputSeconds)"
          class="controlButton"
          type="success"
        >
          REST
        </el-button>
        <!--  :min="1" :max="10" @change="handleChange" -->
        <el-input-number
          type="number"
          class="input"
          v-model="inputMinites"
          controls-position="right"
        ></el-input-number>

        <el-input-number
          class="input"
          controls-position="right"
          v-model="inputSeconds"
        ></el-input-number>
        <el-button
          @click="startTiming(true, inputMinites, inputSeconds)"
          class="controlButton"
          type="success"
        >
          START
        </el-button>
      </div>
    </div>
    <div class="mid">
      <span v-if="!Interval" class="second" id="minute">üëÜËæìÂÖ•Êó∂Èó¥</span>
      <template v-if="Interval">
        <span class="passtime">{{ remainMinites }}</span>
        <span class="second">ÂàÜ</span>
        <span class="passtime">{{ remainSeconds }}</span>
        <span class="second">Áßí</span>
      </template>

      <span class="second" id="second"></span>
      <span id="rest_1" class="tip">‰πãÂêéÔºåÊàêÈïø‰∏∫{{ period }}üå≥</span>
      <br />
      <span id="rest_2" class="second">ÊÇ®Â∑≤Áªè{{ status }}‰∫Ü</span>

      <span class="passtime">{{ passMinites }}</span>
      <span class="second">ÂàÜ</span>
      <span class="passtime">{{ passSeconds }}</span>
      <span class="second">Áßí</span>
    </div>
    <div>
      <el-button @click="addStudyTime" class="controlButton" type="primary">
        Êü•Áúã‰Ωú‰∏ö
      </el-button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState, mapMutations, mapActions } from "vuex";
export default {
  data() {
    return {
      status: "‰∏ìÊ≥®",
      //ËæìÂÖ•Êó∂Èó¥
      inputMinites: 25,
      inputSeconds: 0,
      //Â≠¶‰π†Êó∂Èó¥
      studyMinites: 0,
      studySeconds: 0,
      //ËøáÂéªÊó∂Èó¥
      passMinites: 0,
      passSeconds: 0,
      //Ââ©‰∏ãÁöÑÊó∂Èó¥
      remainMinites: 0,
      remainSeconds: 0,
      //ÊúâÊó†ÂºÄÂßã
      hasStart: false,
      //ËÆ°Êó∂Âô®
      Interval: 0,
    };
  },
  computed: {
    ...mapState("time", ["todayTime", "period"]),
    // remainMinites() {
    //   return this.studyMinites - this.passMinites;
    // },
    // remainSeconds() {
    //   return 60 - this.passSeconds;
    // },
  },
  methods: {
    ...mapActions("time", { addTime: "addTime" }),
    ...mapMutations("time", { clearTodayTime: "clearTodayTime" }),
    addStudyTime() {
      this.clearTodayTime();
    },
    startTiming(value, inputMinites, inputSeconds) {
      this.setStatus(value);
      this.studyMinites = inputMinites;
      this.studySeconds = inputSeconds;

      //1. Ââ©‰∏ãÊó∂Èó¥Á≠â‰∫éËæìÂÖ•Êó∂Èó¥
      //2. ÊØèÁßí-1
      // 2.1 Ëã•Ââ©‰∏ãÊó∂Èó¥ == 0ÔºåMinites-1
      this.remainMinites = inputMinites;
      this.remainSeconds = inputSeconds;

      this.passSeconds = 0;
      this.passMinites = 0;
      if (!this.Interval) {
        this.Interval = setInterval(() => {
          //ÊØèÁßí+1,-1
          this.passSeconds += 1;
          this.remainSeconds -= 1;

          //Á≠â‰∫é60ÔºåÂä†1ÂàÜÈíü
          if (this.passSeconds == 60) {
            this.passSeconds = 0;
            this.passMinites += 1;
          }
          //ÂáèÂà∞-1, Á≠â‰∫é59
          if (this.remainSeconds < 0) {
            this.remainSeconds = 59;
            this.remainMinites -= 1;
          }
          //Êó∂Èó¥Âà∞‰∫Ü
          if (
            this.passMinites === this.studyMinites &&
            this.passSeconds === this.studySeconds
          ) {
            this.stopTiming(value);
          }
        }, 1000);
      }
    },
    stopTiming(value) {
      window.clearTimeout(this.Interval);
      this.Interval = 0;
      if (value) {
        this.open("ÊÇ®Â∑≤Áªè‰∏ìÊ≥®‰∫Ü" + this.inputMinites + "ÂàÜÈíü!Ôºå‰ºëÊÅØ‰∏Ä‰∏ãÂêß")
        // alert("ÊÇ®Â∑≤Áªè‰∏ìÊ≥®‰∫Ü" + this.inputMinites + "ÂàÜÈíü!Ôºå‰ºëÊÅØ‰∏Ä‰∏ãÂêß");
        this.addTime(this.studyMinites);
        if (this.studyMinites >= 5) {
          this.startTiming(false, this.studyMinites / 5, 0);
        } else {
          this.startTiming(false, 1, 0);
        }
      } else {
        open("‰ºëÊÅØÊó∂Èó¥Âà∞ÔºåÂºÄÂßãÂ≠¶‰π†Âôú");
      }
    },
    setStatus(value) {
      if (value) {
        this.status = "‰∏ìÊ≥®";
      } else {
        this.status = "‰ºëÊÅØ";
      }
    },
    open(message) {
      this.$alert(message, "Êó∂Èó¥Âà∞Âôú", {
        confirmButtonText: "Á°ÆÂÆö",
      });
    },
  },
};
</script>

