//ÂºïÂÖ•Vue
import Vue from "vue";
import VueRouter from "vue-router";
//‰ΩøÁî®Êèí‰ª∂
Vue.use(VueRouter);

import Home from '@/pages/Home'
import Login from '@/pages/Login'
import Register from '@/pages/Register'
import Time from '@/pages/Time'

//‚≠êqueryÈÇ£‰∫õÂèÇÊï∞Âë¢
// ÊòØ‰∏Ä‰∏™ÂØπË±°‰º†ËøáÂéªÁöÑ
//ÈúÄË¶ÅÈáçÂÜôVueRouter.prototypeÂéüÂûãÂØπË±°Ë∫´‰∏äÁöÑpush|replaceÊñπÊ≥ï
//ÂÖàÊääVueRouter.prototypeË∫´‰∏äÁöÑpush|replaceÊñπÊ≥ïËøõË°å‰øùÂ≠ò‰∏Ä‰ªΩ
let originPush = VueRouter.prototype.push;
let originReplace = VueRouter.prototype.replace;
//ÈáçÂÜôVueRouter.prototypeË∫´‰∏äÁöÑpushÊñπÊ≥ï‰∫Ü

VueRouter.prototype.push = function(location, resolve, reject) {
  //Á¨¨‰∏Ä‰∏™ÂΩ¢ÂèÇÔºöË∑ØÁî±Ë∑≥ËΩ¨ÁöÑÈÖçÁΩÆÂØπË±°Ôºàquery|paramsÔºâ
  //Á¨¨‰∫å‰∏™ÂèÇÊï∞Ôºöundefined|ÁÆ≠Â§¥ÂáΩÊï∞ÔºàÊàêÂäüÁöÑÂõûË∞ÉÔºâ
  //Á¨¨‰∏â‰∏™ÂèÇÊï∞:undefined|ÁÆ≠Â§¥ÂáΩÊï∞ÔºàÂ§±Ë¥•ÁöÑÂõûË∞ÉÔºâ
  if (resolve && reject) {
    //pushÊñπÊ≥ï‰º†ÈÄíÁ¨¨‰∫å‰∏™ÂèÇÊï∞|Á¨¨‰∏â‰∏™ÂèÇÊï∞ÔºàÁÆ≠Â§¥ÂáΩÊï∞Ôºâ
    //originPushÔºöÂà©Áî®call‰øÆÊîπ‰∏ä‰∏ãÊñáÔºåÂèò‰∏∫(Ë∑ØÁî±ÁªÑ‰ª∂.$router)Ëøô‰∏™ÂØπË±°ÔºåÁ¨¨‰∫åÂèÇÊï∞ÔºöÈÖçÁΩÆÂØπË±°„ÄÅÁ¨¨‰∏â„ÄÅÁ¨¨Âõõ‰∏™ÂèÇÊï∞ÔºöÊàêÂäüÂíåÂ§±Ë¥•ÂõûË∞ÉÂáΩÊï∞
    originPush.call(this, location, resolve, reject);
  } else {
    //pushÊñπÊ≥ïÊ≤°Êúâ‰∫ßÂú∞Á¨¨‰∫å‰∏™ÂèÇÊï∞|Á¨¨‰∏â‰∏™ÂèÇÊï∞
    originPush.call(
      this,
      location,
      () => {},
      () => {}
    );
  }
};
//ÈáçÂÜôVueRouter.prototypeË∫´‰∏äÁöÑreplaceÊñπÊ≥ï‰∫Ü
VueRouter.prototype.replace = function(location, resolve, reject) {
  if (resolve && reject) {
    originReplace.call(this, location, resolve, reject);
  } else {
    originReplace.call(
      this,
      location,
      () => {},
      () => {}
    );
  }
};

export default new VueRouter({
    routes: [
        {
            path:"/home",
            component:Home,
            meta:{
                isShow:true
            }
        }
        ,
        {
            name: "time",
            // Âä†‰∏ä?, Ë°®Á§∫ÂèØ‰º†ÂèØ‰∏ç‰º†
            path:"/time",
            component:Time,
            meta:{
                isShow:true
            },
        }
        ,
        {
            path:"/Login",
            component:Login
        },
        {
            path:"/Register",
            component:Register
        },
        //ÈáçÂÆöÂêë, Âú®È°πÁõÆË∑ëËµ∑Êù•ÁöÑÊó∂ÂÄôÂÆöÂêëÂà∞È¶ñÈ°µ
        {
            path:"*",
            redirect:"/time"
        }
    ]
})
